
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}



ul {
  list-style: none
}

li::before {
  content: "\2022";
  color: #FA4A0C;
  display: inline-block;
  width: 1em;
  margin-left: -0.5em;
}


@font-face {
  font-family: customFont;
  src: url(IBMPlexSans-Light.ttf)
}


@font-face {
  font-family: customFont2;
  src: url(IBMPlexSans-Light.ttf)
}


body {
  background-color: #EDEDED;;
  background-color: white;
  font-family: customFont;
}


/*
contact styling
*/

#contactview-container {
backdrop-filter: blur(3px);
-webkit-backdrop-filter: blur(3px);
z-index: 1;
display: flex;
width: 100vw;
height: 100vh;
position: fixed;
top: 0px;
bottom: 0px;
justify-content: center;
align-items: center;
z-index: 3;
overflow:scroll;
padding: 20px;
}

#contact-option-card {
  column-gap: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255,255,255,.12);
  width: 200.29px;
  height:90.62px;
  border-radius: 18.264px;
}

#contact-card {
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-top: auto;
  margin-bottom: auto;
  row-gap: 20px;
  position: relative;
  padding: 30px;
  margin-inline: auto;
  height: 300px;
  border-radius: 40px;
  background-color: rgb(29, 29, 31, .95);
  box-shadow: 0px 25.704px 48.704px 10.352px rgba(0, 0, 0, .35);
}

#contact-card  {
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}



/*
Experience view styling
*/

#experience-container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  max-width: 804px;
  height: 'auto';
  background-color: 'white';
  padding: 20px;
  row-gap: 10px
}

#job-info-container {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
}

#job-achievements-container {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

#experience-view-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px
}

/*
Landing view styling
*/

#landing-view-container {
  width: 100%;
  height: auto;
  flex-direction: column;
  background-color: white;
  display: flex;
    justify-content: center;
    align-items: center;
  min-height: 100vh;
  
}

#bio-container {
  padding: 20;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

/*
sidebar styling
*/

#sidebar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90px;
  width: 100%
}

#sidebar {
  background-color: rgb(29, 29, 31);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, .1);
  position: fixed;
  top: 10px;
  min-height: 80px;
  border-radius: 21.31px;
  width: calc(100% - 20px);
  max-width: 700px;
  overflow: hidden;
  z-index: 1;
}

#expanded-sidebar-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 700px;
  width: 100%;
  height: 100%;
}

#top-sidebar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items:  center;
  width: calc(100% - 10px);
  position: absolute;
  top: 15px;
}

/*
project view
*/

#project-view-container {
  flex-direction: column;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center
}
.project-card {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  justify-content: center;
  align-items: center;
  position: relative;
  min-width: 120px;

}
#gallery-container {
  display: flex;
   width: 100%;
   justify-content: center;
    align-items: center;
}
/*
project detail
*/

.project-grid-item {
  place-self: center stretch;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  background-color: rgb(29, 29, 31);
  height: 100%;
  overflow: hidden;
  padding: 30px;
  box-shadow: 0px 30px 60px rgba(57, 57, 57, 0.1);
  width: 100%
}

#project-detail-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 10px;
}

#project-gall-container {
  display: flex;
  justify-content: flex-start;
  overflow-x: auto;
  flex-direction: row;
  align-items: flex-end;
  column-gap: 50px;
  padding: 30px;
}

#project-gall-container::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}

#project-grid-container {
  display: grid;
  grid-template-columns: 16rem 8rem 20rem;
  grid-template-rows: auto auto auto;
  column-gap: 10px;
  row-gap: 10px;
}
.project-grid-item {
  grid-column: 1/span 2;
  grid-row: 1/span 1; 
}

.project-grid-item:nth-child(2) {
  grid-column: 3/span 1;
  grid-row: 1/span 1; 
}

.project-grid-item:nth-child(3) {
  grid-column: auto/span 3;
  grid-row: auto/span 1; 
  
}

.project-grid-item:nth-child(4) {
  grid-column: 2/span 2;
  grid-row: 2/span 1; 
}
.project-grid-item:nth-child(5) {
  grid-column: 1/span 1;
  grid-row: 2/span 1; 
}

@media only screen and (max-width: 734px) {
  #project-grid-container  {
    grid-template-columns: auto;
    grid-auto-rows: auto;
    max-width: 25em;
    width: 100%;
}
#project-grid-container  * {
  grid-column: auto;
  grid-row: auto;
}


}